const data = window.localStorage.getItem('data');
var info;
if(data === null) {
    info = {
        expiration: 0,
        fullName: "",
        token: "",
        username: ""
    }
} else {
    info = JSON.parse(data);
}
class Token {
    info;
    constructor(info) {
        this.info = info;
    }
}
export default new Token(info);