import { useEffect, useState } from "react";
import React from "react"
import MajorService from "../services/MajorService";
import SubjectService from "../services/SubjectService";
import { Box, FormControl, InputLabel, MenuItem, Select } from "@mui/material";
import Button from '@mui/material/Button';
import { useNavigate } from "react-router-dom";
import AdmService from "../services/AdmService";
function AddSubjectForMajorComponent() {
    const [majors, setMajors] = useState([]);
    const [subjects, setSubjects] = useState([]);
    const [courses, setCourses] = useState([]);
    const [courseId, setCourseId] = useState('');
    const [semesterId, setSemesterId] = useState('');
    const nav = useNavigate();
    useEffect(() => {
        getAllMajor();
        getAllCourse();
    }, [])

    const handleClick = () => {
        nav("/admin")
    }

    const getAllMajor = () => {
        MajorService.getAllMajors().then(res => {
            setMajors(res.data);
        }).catch(err => { console.log(err) });
    }

    const handleChange = (e) => {
        SubjectService.getAllSubjectByMajorId(e.target.value)
            .then(res => {
                setSubjects(res.data);
            }).catch(err => {
                alert(err.status);
            })
    }
    
    const getAllCourse = () => {
        AdmService.getAllCourse().then(res => {
            setCourses(res.data);
        }).catch(err => {console.log(err.status)})
    }
    const handleRegister = (e) => {
        const subjectId = e.target.value;
        const register = {

        }
        AdmService.registerSubjectForMajor()
    }
    return (
        <div style={{
            marginTop: '50px'
        }}>
            <Box sx={{ minWidth: 120 }}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Năm học</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Năm học"
                        onChange={(e) => {
                            const data = e.target.value.split('_');
                            setCourseId(data[0])
                            setSemesterId(data[1])
                        }}
                    >
                       {courses.map((course) => {
                            return course.semesters.map((semester) => {
                                return (
                                <MenuItem
                                    value={course.id + '_' + semester.id}
                                    key={course.id + '-' + semester.id}
                                >

                                   Năm học {course.courseDate + " " + semester.semester}
                                </MenuItem>
                                )
                            })
                       })}
                    </Select>
                </FormControl>
            </Box>
            <br/>
            <Box sx={{ minWidth: 120 }}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Chuyên ngành</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Chuyên ngành"
                        onChange={handleChange}
                    >
                        {majors.map((major) => {
                            return (
                                <MenuItem
                                    value={major.id}
                                    key={major.id}
                                >

                                    {major.name}
                                </MenuItem>
                            )
                        })}
                    </Select>
                </FormControl>
            </Box>
            <br/>
            <table className="table table-bordered border-primary">
                <thead>
                    <tr>
                        <th>Mã môn học</th>
                        <th>Tên môn học</th>
                        <th>Số tín chỉ</th>
                        <th>Đăng ký</th>

                    </tr>
                </thead>
                <tbody>
                    {subjects.map(subject => {

                        return (
                            (
                                <tr key={subject.id}>
                                    <td>
                                        {subject.subjectCode}
                                    </td>
                                    <td>
                                        {subject.subjectName}
                                    </td>
                                    <td>
                                        {subject.credit}
                                    </td>
                                    
                                    <td>
                                        <div className='form-check'>
                                            <input
                                                className='form-check-input'
                                                style={{
                                                    cursor: 'pointer'
                                                }}
                                                checked={subject.check}
                                                type={"checkbox"}
                                                value={subject.id}
                                                onChange={(e) => {
                                                    handleRegister(e)
                                                }}
                                                disabled={subject.check}

                                            />
                                        </div>
                                    </td>
                                </tr>
                            )
                        )

                    })}
                </tbody>
            </table>
                <Button 
                variant="contained"
                onClick={handleRegister}
              >Hủy bỏ</Button>
              <Button 
                variant="contained"
                onClick={handleClick}
              >Hủy bỏ</Button>
        </div>

    )
}

export default AddSubjectForMajorComponent;