import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import { BrowserRouter, Link } from "react-router-dom";
import Token from '../services/Token';
const pages = [
  {
    page: "Đăng ký môn học",
    link: "/dang-ky-mon-hoc"
  },
  {
    page: "Trao đổi môn học",
    link: "/trao-doi-mon-hoc"
  },
  {
    page: "Thời khóa biểu",
    link: "/thoi-khoa-bieu"
  },
  {
    page: "Xem học phí",
    link: "/xem-hoc-phi"
  },
  {
    page: "Xem điểm",
    link: "/xem-diem"
  }
];
const settings = [
  {
    name: "Thông tin cá nhân",
    link: "/thong-tin-ca-nhan"
  },
  {
    name: "Đổi mật khẩu",
    link: "/doi-mat-khau"
  },
  {
    name: "Quản lý vai trò",
    link: "/admin"
  },
  {
    name: "Các môn đang trao đổi",
    link: "/thong-tin-trao-doi-mon-hoc"
  },
]

function HeaderComponent() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component={Link}
            to="/react-gh-pages"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            LOGO
          </Typography>
          {/* <Button
          variant="contained"
          color="primary"
          component={RouterLink}
          to="/dang-nhap"
        >LOGO</Button> */}

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page.page} onClick={handleCloseNavMenu}>
                  <Typography
                    textAlign="center"
                    variant="h6"
                    noWrap
                    component={Link}
                    to={page.link}
                    sx={{
                    
                      color: 'inherit',
                      textDecoration: 'none',
                    }}
                  >{page.page}
                  </Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
          <Typography
            variant="h5"
            noWrap
            component={Link}
            href="/react-gh-pages"
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            LOGO
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            {pages.map((page) => (
              <Button
                onClick={handleCloseNavMenu}
                sx={{ my: 1, color: 'white', display: 'block' }}
                component={Link}
                to={page.link}
              >
                {page.page}
              </Button>
            ))}
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            {new Date().getTime() > new Date(Token.info.expiration) ? (
              <Button
                onClick={handleCloseNavMenu}
                sx={{ my: 1, color: 'white', display: 'inline' }}
                component={Link}
                to={"/dang-nhap"}
              >
                Đăng nhập
              </Button>
            ) : (
              <div>
                <Tooltip title={Token.info.username}>

                  <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                    <Avatar alt="Remy Sharp" src="/static/images/avatar/2.jpg" />
                  </IconButton>
                </Tooltip>
                <Menu
                  sx={{ mt: '50px' }}
                  id="menu-appbar"
                  anchorEl={anchorElUser}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={Boolean(anchorElUser)}
                  onClose={handleCloseUserMenu}
                >
                  {settings.map((setting) => (
                    <MenuItem key={setting.name} onClick={handleCloseUserMenu}>
                      <Typography 
                      textAlign="center"
                      component={Link}
                      to={setting.link}
                      sx={{
                        mr: 3,
                        // display: { xs: 'none', md: 'flex' },
                        // fontFamily: 'monospace',
                        fontSize: '20px',
                        // fontWeight: 700,
                        // letterSpacing: '.3rem',
                        color: 'inherit',
                        textDecoration: 'none',
                      }}
                      >{setting.name}</Typography>
                    </MenuItem>
                  ))}
                </Menu>
              </div>
            )}


          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default HeaderComponent;
