import axios from 'axios';
import Token from './Token';
const ENDPOIT_COURSE = "https://localhost:9191/api/courses";
const ENDPOIT_REGISTER_MAJOR = "http://localhost:9191/api/subject/major"
class AdmService {
    addCourse(course) {
        let data = JSON.stringify(course);
        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `${ENDPOIT_COURSE}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            },
            data: data
        };

        return axios.request(config);
    }

    getAllCourse() {
        let data = '';
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: 'http://localhost:9191/api/courses',
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            },
            data: data
        };
        return axios.request(config);

    }

    registerSubjectForMajor(register) {
        let data = JSON.stringify(register);
        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `${ENDPOIT_REGISTER_MAJOR}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            },
            data: data
        };

        return axios.request(config);
    }
}
export default new AdmService();
