import { useState } from "react"
import AdmService from "../services/AdmService"
import { Link } from "react-router-dom";
function AddCourseComponent () {
        const [courseDate, setCourseDate] =useState('');
        const [prefixUsername, setPrefixUsername] = useState('');
        const addCourse = () => {
            const course = {
                courseDate, prefixUsername
            }
            AdmService.addCourse(course).then((res)=> {
                alert(res.status)
            }).catch((err) => {alert(err.status)})
        }
    return (
        <div>
            <br />
            <br />
            <div className="container">
                <div className="row">
                    <div className="card col-md-6 offset-md-3 offset-md-3">
                        {/* {title()} */}
                        <div className="card-body">
                            <form method="post">
                                <div className="form-group mb-2">
                                    <label className="form-label">
                                       Năm học:
                                    </label>
                                    <input
                                        type="text"
                                        placeholder="Ví dụ: 2023-2024"
                                        className="form-control"
                                        value = {courseDate}
                                        onChange={(e) => {
                                            setCourseDate(e.target.value);
                                        }}
                                    />
                                </div>
                                <div className="form-group mb-2">
                                    <label className="form-label">
                                       Khóa sinh viên:
                                    </label>
                                    <input
                                        type="text"
                                        placeholder="Ví dụ: D22"
                                        className="form-control"
                                        value = {prefixUsername}
                                        onChange={(e) => {
                                            setPrefixUsername(e.target.value);
                                        }}
                                    />
                                </div>
                                <button onClick={addCourse} className="btn btn-success">Đăng ký</button>
                                <Link to="/admin" className="btn btn-danger">Hủy bỏ</Link>
                            </form>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}
export default AddCourseComponent;
